/*
 * @Author:  
 * @Date: 2018-03-06 19:27:24 
 * @Last Modified by: 
 * @Last Modified time: 2018-03-17 17:39:01
 * --
 */

 <template>
  <div id="prodQueryModal">
    <Scroll class="scroll-conntent" :data="status">
      <div>
        <div>
          <p class="title"></p>
          <div>
            <span v-for="(item,index) in pKeyList[0]" :key="index" :class="{ActiveIndex:ActiveIndexList[0]===index}" @click="handleChange(0,index)">{{item}}</span>
          </div>
        </div>
        <div>
          <p class="title"></p>
          <div>
            <span v-for="(item,index) in pKeyList[1]" :key="index" :class="{ActiveIndex:ActiveIndexList[1]===index}" @click="handleChange(1,index)">{{item}}</span>
          </div>
        </div>
        <div>
          <p class="title"></p>
          <div>
            <span v-for="(item,index) in pKeyList[2]" :key="index" :class="{ActiveIndex:ActiveIndexList[2]===index}" @click="handleChange(2,index)">{{item}}</span>
          </div>
        </div>
        <div>
          <p class="title"></p>
          <div>
            <span v-for="(item,index) in pKeyList[3]" :key="index" :class="{ActiveIndex:ActiveIndexList[3]===index}" @click="handleChange(3,index)">{{item}}</span>
          </div>
        </div>
        <div>
          <p class="title"></p>
          <div>
            <span v-for="(item,index) in pKeyList[4]" :key="index" :class="{ActiveIndex:ActiveIndexList[4]===index}" @click="handleChange(4,index)">{{item}}</span>
          </div>
        </div>
        <div>
          <p class="title"></p>
          <div>
            <span v-for="(item,index) in pKeyList[5]" :key="index" :class="{ActiveIndex:ActiveIndexList[5]===index}" @click="handleChange(5,index)">{{item}}</span>
          </div>
        </div>
        <div>
          <p class="title"></p>
          <div>
            <span v-for="(item,index) in pKeyList[6]" :key="index" :class="{ActiveIndex:ActiveIndexList[6]===index}" @click="handleChange(6,index)">{{item}}</span>
          </div>
        </div>
      </div>
    </Scroll>
    <div class="footer-btn-content">
      <span class="reset" @click="reset"></span>
      <span class="confirm" @click="confirm"></span>
    </div>
  </div>
</template>

<script type="es6">
import Scroll from "base/scroll/scroll";
export default {
  props: {
    //   
    initList: {
      type: Array,
      default: function() {
        return [0, 0, 0, 0, 0, 0, 0];
      }
    },
    // key
    pKeyList: {
      type: Array,
      default: function() {
        return [
          /*  */
          /*  */
          // ["","","","",""],
          ["", "", "", ""],
          /*  */
          ["", "12", "12", "13-23", "24", "24"],
          /*  */
          [
            "",
            "7%",
            "7%()-8%",
            "8%()-9%",
            "9%()-10%",
            "10%",
            ""
          ],
          /*  */
          [
            "",
            "",
            "",
            "",
            "",
            ""
          ],
          /*  */
          [
            "",
            "",
            "",
            "",
            "",
            "",
            ""
          ],
          /*  */
          ["", "50", "100", "200", "300"],
          /*  */
          ["", "", "", "", ""]
        ];
      }
    },
    status: ""
  },
  data() {
    return {
      ActiveIndexList: [0, 0, 0, 0, 0, 0, 0] //
      // KeyList: []
    };
  },
  methods: {
    //   
    confirm() {
      this.$emit("confirm", this.ActiveIndexList);
    },
    // 
    reset() {
      this.$emit("reset");
      this.ActiveIndexList = [...this.initList];
    },
    handleChange(key, value) {
      this.$set(this.ActiveIndexList, key, value);
    }
  },
  watch: {
    initList() {
      //   
      this.ActiveIndexList = [...this.initList];
    }
  },
  components: {
    Scroll
  }
};
</script>

<style lang="stylus">
#prodQueryModal {
  height: 100%;
  width: 100%;
  position: relative;

  >.scroll-conntent {
    width: 100%;
    position: absolute;
    overflow: hidden;
    bottom: 34px;
    top: 0;

    >div {
      >div {
        padding: 16px;
        border-bottom: 1px solid #BFBFBF;

        .title {
          font-size: 14px;
          color: #333333;
          margin-bottom: 16px;
        }

        >div {
          >span {
            display: inline-block;
            padding: 4px 8px;
            margin: 6px;
            border: 1px solid #BFBFBF;
            border-radius: 16px;
          }

          .ActiveIndex {
            background-color: red;
            color: #FFFFFF;
          }
        }
      }
    }
  }

  .footer-btn-content {
    height: 34px;
    width: 100vw;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: absolute;
    bottom: 0;

    >span {
      flex: 1;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 14px;
    }

    .reset {
      background: #D8D8D8;
      color: #333333;
    }

    .confirm {
      background: #E10101;
      color: #FFFFFF;
    }
  }
}
</style>

 
 



