/*
 * @Author:  
 * @Date: 2018-03-05 10:11:06 
 * @Last Modified by: 
 * @Last Modified time: 2018-03-17 15:57:36
 * ----
 */

<template>
  <div class="gatherTrust">
    <conditionTab @onClick="get_tabsIndex"></conditionTab>
    <Scroll class="scroll-conntent" :data="productList" :pullup="pullup" @scrollToEnd="scrollToEnd">
      <div>
        <product-item v-for="(item,index) in productList" :key="index" :productItem="item"></product-item>
        <loading v-if="pullup"></loading>
        <to-end v-else></to-end>
      </div>
    </Scroll>
    <mt-popup v-model="popupVisible" position="bottom">
      <prodQueryModal :status="popupVisible" @reset="pQueryReset" @confirm="confirm"></prodQueryModal>
    </mt-popup>
  </div>
</template>

<script type="es6">
import ajax from "api/ajax";
import Scroll from "base/scroll/scroll";
import loading from "base/loading/loading";
import toEnd from "base/toEnd/toEnd";
import productItem from "components/pro_product/productItem";
import prodQueryModal from "components/ProdQueryModal/prodQueryModal";
import conditionTab from "components/conditionTab/conditionTab";

export default {
  data() {
    return {
      /*  */
      ptype: "04",
      productList: [],
      loading: false,
      /**
       * 
       */
      popupVisible: false, //
      initList: [0, 0, 0, 0, 0, 0, 0], //--item
      /**
       * 
       */
      saleStatus: "", // 01 02 03 04
      dueTime: "", // 0102030405
      annualRevenue: "", //010203040506
      pPaymentInterestType: "", //01 02 03 04 05 
      pInvestType: "", //01 02 03 04 05 99
      minimumAmount: "", //
      pSizeRatioType: "", //01 02 03 04
      pRabateProfitParameter: false, // true
      pAnnualRevenueExpectParameter: false, // true
      /**
       * 
       */
      pullup: true, //
      pageNo: 1 //
    };
  },
  created() {
    this.get_proList();
  },
  methods: {
    // 
    scrollToEnd() {
      if (this.pullup) {
        this.pageNo++;
        this.get_proList();
      }
    },
    /**
     * @param index 
     */
    get_tabsIndex(index) {
      let url;
      switch (index) {
        case 0:
          this.pageNo = 1;
          this.pullup = true;
          this.initList = [0, 0, 0, 0, 0, 0, 0];
          this.productList = [];
          this.get_proList();
          break;
        case 1:
          this.pageNo = 1;
          this.pullup = true;
          this.productList = [];
          url = `/srv/v1/product/list?pageNo=${this.pageNo}&pageSize=${
            this.$store.state.pageSize
          }&type=${this.ptype}&saleStatus=0${this.initList[0]}&dueTime=0${
            this.initList[1]
          }&annualRevenue=0${this.initList[2]}&pPaymentInterestType=0${
            this.initList[3]
          }&pInvestType=0${this.initList[4]}&minimumAmount=0${
            this.initList[5]
          }&pSizeRatioType=0${this.initList[6]}&pRabateProfitParameter=true`;
          this.get_proList(url);
          break;
        case 2:
          this.pageNo = 1;
          this.pullup = true;
          this.productList = [];
          url = `/srv/v1/product/list?pageNo=${this.pageNo}&pageSize=${
            this.$store.state.pageSize
          }&type=${this.ptype}&saleStatus=0${this.initList[0]}&dueTime=0${
            this.initList[1]
          }&annualRevenue=0${this.initList[2]}&pPaymentInterestType=0${
            this.initList[3]
          }&pInvestType=0${this.initList[4]}&minimumAmount=0${
            this.initList[5]
          }&pSizeRatioType=0${
            this.initList[6]
          }&pAnnualRevenueExpectParameter=true`;
          this.get_proList(url);
          break;
        // 
        case 3:
          this.popupVisible = true;
          break;
        default:
          break;
      }
    },
    /**
     *  
     */
    get_proList(
      url = `/srv/v1/product/list?pageNo=${this.pageNo}&pageSize=${
        this.$store.state.pageSize
      }&type=${this.ptype}&saleStatus=0${this.initList[0]}&dueTime=0${
        this.initList[1]
      }&annualRevenue=0${this.initList[2]}&pPaymentInterestType=0${
        this.initList[3]
      }&pInvestType=0${this.initList[4]}&minimumAmount=0${
        this.initList[5]
      }&pSizeRatioType=0${this.initList[6]}`
    ) {
      ajax({
        url,
        method: "GET"
      }).then(res => {
        if (res.status === 200) {
          if (res.data.result.pager) {
            this.pullup = res.data.result.pager.hasNaxtPage;
            this.productList = [...this.productList, ...res.data.result.list];
          } else {
            this.pullup = false;
          }
        }
      });
    },
    /**
     * @event 
     */
    confirm(parmas) {
      this.initList = parmas;
      this.popupVisible = false;
      this.pageNo = 1;
      this.productList = [];
      this.get_proList();
    },
    /**
     * @event 
     */
    pQueryReset() {
      this.initList = [0, 0, 0, 0, 0, 0, 0];
      // this.popupVisible = false;
    }
  },
  components: {
    Scroll,
    productItem,
    conditionTab,
    loading,
    toEnd,
    prodQueryModal
  }
};
</script>

<style lang="stylus">
.gatherTrust {
  position: relative;
  width: 100%;
  height: calc(100% - 90px);

  .tabs-content {
  }

  /*  */
  >.scroll-conntent {
    position: absolute;
    top: 34px;
    bottom: 0;
    overflow: hidden;
    width: 100%;
  }
}
</style>





