/*
 * @Author:  
 * @Date: 2018-03-04 22:28:31 
 * @Last Modified by: 
 * @Last Modified time: 2018-03-17 15:59:10
 * 
 */

<template>
  <div id="product">
    <header ref="tabsContent" class="tabs-content" @click="set_tabs">
      <span v-for="(item,index) in headerTabList" :class="{currSpan:activeIndex===index}" :data-index="index" :key="index">{{item}}</span>
    </header>
    <gatherTrust v-if="activeIndex===0"></gatherTrust>
    <gatherZG v-if="activeIndex===1"></gatherZG>
    <bondFund v-if="activeIndex===2"></bondFund>
    <sunPrivate v-if="activeIndex===3"></sunPrivate>
    <equityFund v-if="activeIndex===4"></equityFund>
  </div>
</template>

<script type="es6">
import Scroll from "base/scroll/scroll";
import gatherTrust from "components/gatherTrust/gatherTrust"; //
import gatherZG from "components/gatherZG/gatherZG"; //
import bondFund from "components/bondFund/bondFund"; //
/*  */
import sunPrivate from "components/sunPrivate/sunPrivate"; //
import equityFund from "components/equityFund/equityFund"; //

export default {
  data() {
    return {
      // tabs
      headerTabList: [
        "",
        "",
        "",
        ""
        // ""
      ],
      activeIndex: 0 //tab
    };
  },
  created() {
    console.log(this.$route.query.activeIndex);
    this.activeIndex = parseInt(this.$route.query.activeIndex);
  },
  methods: {
    // 
    set_tabs(e) {
      // tabsindexhistory
      // 
      const currobjKey = e.target.getAttribute("data-index");
      this.$router.replace({
        path: `/product`,
        query: { activeIndex: currobjKey }
      });
      this.activeIndex = parseInt(currobjKey);
    }
  },
  components: {
    Scroll,
    gatherTrust,
    gatherZG,
    bondFund,
    sunPrivate,
    equityFund
  }
};
</script>

<style lang="stylus" scoped>
#product {
  position: relative;
  width: 100%;
  height: 100%;
  // top: 40px;
  top: 0;
  bottom: 46px;
  background: #fff;
  overflow: hidden;

  // 
  .tabs-content {
    height: 50px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-top: 1px solid #de0b17;
    background-color: #eff3f6;
    padding-bottom: 10px;

    >span {
      display: inline-block;
      height: 50px;
      line-height: 50px;
      flex: 1;
      text-align: center;
      font-size: 14px;
      margin-bottom: -10px;
      color: #333;
    }

    .currSpan {
      color: #FFFFFF;
      background-color: #de0b17;
    }
  }
}
</style>





