 <template>
  <div id="order">
    <mt-header title="">
      <mt-button icon="back" @click="back" slot="left"></mt-button>
    </mt-header>
    <Scroll id="scroll">
      <div class="scroll-content">
        <mt-field label="" disabled v-model="pName"></mt-field>
        <!-- TODO: -->
        <mt-field label="" disabled placeholder="" v-model="customerName" @click.native="updatePickerStatus('CustomersModal')">
          <i class="fa fa-address-card"></i>
        </mt-field>
        <mt-field label="" type="number" placeholder="" v-model="cardId"></mt-field>
        <!-- <mt-field label="" placeholder="" v-model="phone"></mt-field> -->
        <mt-field label="" type="number" placeholder="" v-model="bankCardNo"></mt-field>
        <mt-field label="" disabled placeholder="" v-model="bankName" @click.native="updatePickerStatus('bankCardPickStatus')">
          <i class="fa fa-credit-card"></i>
        </mt-field>
        <mt-field label="" type="number" placeholder="" v-model="amount">
          <span></span>
        </mt-field>
        <mt-field label="" placeholder="" disabled v-model="pLatestPayNum">
          <!-- <mt-field label="" placeholder="" disabled v-model="pLatestPayNum" @click.native="openDatePicker"> -->
          <!-- <i class="fa fa-calendar"></i> -->
        </mt-field>
        <mt-field label="" placeholder="" type="textarea" rows="4" v-model="note"></mt-field>
      </div>
    </Scroll>
    <!--  -->
    <footer class="footer">
      <span class="submit" @click="submit"></span>
    </footer>
    <mt-datetime-picker type="date" ref="picker" v-model="pickerValue" year-format="{value} " month-format="{value} " date-format="{value} " @confirm="handleConfirm">
    </mt-datetime-picker>
    <!-- <mt-popup v-model="CustomersModal" position="bottom">
      <mt-picker ref='pickerObj' @change="onValuesChange" :slots="Customers" valueKey="name"></mt-picker>
    </mt-popup> -->
    <!--  -->
    <pulldown-select :values="Customers" valueKey="name" @confirm="onValuesChange" @cancel="updatePickerStatus('CustomersModal')" v-if="CustomersModal"></pulldown-select>
