      if (param === undefined) {
        param = "";
      }
      this.bankName = param;
      this.updatePickerStatus("bankCardPickStatus");
    },
    // 
    updatePickerStatus(params) {
      this[params] = !this[params];
    },
    // 
    onValuesChange(parmas) {
      this.customerName = parmas.name;
      this.customerId = parmas.uid;
      this.updatePickerStatus("CustomersModal");
    },
    /**
     * 
     */
    get_Customers() {
      ajax({
        url: "/srv/v1/order/queryCustomersForOrder",
        method: "GET"
      }).then(res => {
        if (res.status === 200) {
          this.Customers[0].values = res.data.result.list;
        }
      });
    },
    /*  */
    check_reg(param, msg) {
      if (this[param]) {
        return true;
      } else {
        MessageBox("", msg);
        return false;
      }
    },
    // 
    submit() {
      /**
       * 1
       */
      if (
        this.check_reg("customerName", "") &&
        this.check_reg("cardId", "") &&
        this.check_reg("bankCardNo", "") &&
        this.check_reg("bankName", "") &&
        this.check_reg("amount", "") &&
        this.check_reg("pLatestPayNum", "")
      ) {
        const promise = new Promise(function(resolve, reject) {
          /**
           * 2
